.landing {
  position: relative;
  overflow-x: hidden;
}

.landingSplash {
  width: 800px;
  position: absolute;
  top: -0;
  right: 0;

  display: flex;
  align-items: flex-start;

  @media screen and (max-width: 1080px) {
    width: 70vw;
    min-width: 450px;
  }

  img {
    width: 800px;
    max-width: 100%;
    height: auto;
  }
}


.greenWavy {
  position: absolute;
  top: 170px;
  left: -370px;
  width: 600px;
  z-index: -1;

  transition: opacity 0.3s ease;
  @media screen and (max-width: 1630px) {
    opacity: 0;
  }
}

.ellipse {
  position: absolute;
  top: -70px;
  left: -50px;
  width: 200px;
  z-index: -1;
}
